  padding: .3rem !important;
}
.col-md-6.m-left {
  padding-left: 20%;
}
i.fas {
  font-size: 20px;
  color: #9d7e33;
}
.h5 i {
  margin-right: 0.96rem;
  width: 6%;
  text-align: center;
}
a.ali::before {
    position: absolute;
    content: '';
    width: calc(100% - 57px);
    display: table;
    height: 1px;
    border-radius: 3px;
    bottom: 12px;
    right: 5px;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    background: #e5b12f !important;
    opacity: 0;
}
a.ali.router-link-exact-active.router-link-active::before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 1;
    color: #f3c26b !important;
    background-color: transparent;
    text-decoration: none;
}
a.ali.router-link-exact-active.router-link-active {
  opacity: 1;
}








































